.ant-result {
    padding: 48px 32px;
}

.ant-result-success .ant-result-icon > .anticon {
    color: #52c41a;
}

.ant-result-error .ant-result-icon > .anticon {
    color: #ff4d4f;
}

.ant-result-info .ant-result-icon > .anticon {
    color: #1890ff;
}

.ant-result-warning .ant-result-icon > .anticon {
    color: #faad14;
}

.ant-result-image {
    width: 250px;
    height: 295px;
    margin: auto;
}

.ant-result-icon {
    margin-bottom: 24px;
    text-align: center;
}

.ant-result-icon > .anticon {
    font-size: 72px;
}

.ant-result-title {
    color: rgba(0, 0, 0, 0.85);
    font-size: 24px;
    line-height: 1.8;
    text-align: center;
}

.ant-result-subtitle {
    color: rgba(0, 0, 0, 0.45);
    font-size: 14px;
    line-height: 1.6;
    text-align: center;
}

.ant-result-extra {
    margin: 24px 0 0 0;
    text-align: center;
}

.ant-result-extra > * {
    margin-right: 8px;
}

.ant-result-extra > *:last-child {
    margin-right: 0;
}

.ant-result-content {
    margin-top: 24px;
    padding: 24px 40px;
    background-color: #fafafa;
}