.ant-image {
    position: relative;
    display: inline-block;
}

.ant-image-img {
    display: block;
    width: 100%;
    height: auto;
    border-radius: var(--border-radius-xl);
    border: 1px solid transparent;
}

.ant-image-img-placeholder {
    background-color: var(--image-placeholder-bg);
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 30%;
}

.ant-image-mask {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--image-mask-text);
    background: var(--image-mask-bg);
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.3s;
    border-radius: var(--border-radius-xl);
}

.ant-image-mask-info .anticon {
    -webkit-margin-end: .25rem;
    margin-inline-end: .25rem;
}

.ant-image-mask:hover {
    opacity: 1;
}

.ant-image-placeholder {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}

.ant-image-preview {
    pointer-events: none;
    height: 100%;
    text-align: center;
}

.ant-image-preview.ant-zoom-enter,
.ant-image-preview.ant-zoom-appear {
    transform: none;
    opacity: 0;
    -webkit-animation-duration: 0.3s;
    animation-duration: 0.3s;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.ant-image-preview-mask {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1000;
    height: 100%;
    background-color: var(--image-mask-bg);
}

.ant-image-preview-mask-hidden {
    display: none;
}

.ant-image-preview-wrap {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
    outline: 0;
    -webkit-overflow-scrolling: touch;
}

.ant-image-preview-body {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
}

.ant-image-preview-img {
    max-width: 100%;
    max-height: 100%;
    vertical-align: middle;
    transform: scale3d(1, 1, 1);
    cursor: -webkit-grab;
    cursor: grab;
    transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    pointer-events: auto;
}

.ant-image-preview-img-wrapper {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transition: transform 0.3s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
}

.ant-image-preview-img-wrapper::before {
    display: inline-block;
    width: 1px;
    height: 50%;
    margin-right: -1px;
    content: '';
}

.ant-image-preview-moving .ant-image-preview-img {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

.ant-image-preview-moving .ant-image-preview-img-wrapper {
    transition-duration: 0s;
}

.ant-image-preview-wrap {
    z-index: 1080;
}

.ant-image-preview-operations {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-size: var(--font-size-default);
    font-variant: tabular-nums;
    line-height: 1.5rem;
    font-feature-settings: 'tnum';
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    width: 100%;
    color: var(--image-preview-operations-text);
    list-style: none;
    background: var(--image-preview-operations-bg);
    pointer-events: auto;
}

.ant-image-preview-operations-operation {
    margin-left: 12px;
    padding: 12px;
    cursor: pointer;
}

.ant-image-preview-operations-operation-disabled {
    color: var(--image-preview-disabled-operations-text);
    pointer-events: none;
}

.ant-image-preview-operations-operation:last-of-type {
    margin-left: 0;
}

.ant-image-preview-operations-icon {
    font-size: var(--font-size-lg);
}

.ant-image-preview-switch-left,
.ant-image-preview-switch-right {
    position: absolute;
    top: 50%;
    right: .625rem;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.75rem;
    height: 2.75em;
    margin-top: -1.375rem;
    background: var(--image-preview-switch-bg);
    color: var(--image-preview-switch-text);
    border-radius: 50%;
    cursor: pointer;
    pointer-events: auto;
}

.ant-image-preview-switch-left-disabled,
.ant-image-preview-switch-right-disabled {
    color: var(--image-preview-disabled-switch-text);
    cursor: not-allowed;
}

.ant-image-preview-switch-left-disabled > .anticon,
.ant-image-preview-switch-right-disabled > .anticon {
    cursor: not-allowed;
}

.ant-image-preview-switch-left > .anticon,
.ant-image-preview-switch-right > .anticon {
    font-size: var(--font-size-lg);
}

.ant-image-preview-switch-left {
    left: 1rem;
}

.ant-image-preview-switch-right {
    right: 1rem;
}